.component-related-posts {
    .related-post-image {
        border-radius: 50%;
        border: 2px solid var(--bs-secondary-bg);
        transition: transform 150ms;
        box-shadow: var(--ui-shadow);
    }

    .related-post-link:hover .related-post-image {
        transform: scale(1.05);

    }
}